<template>
    <div>
        <Row>
            <!-- v-permission.show="'b:perm:ep:create'" -->
            <Dropdown @on-click="create">
                <Button type="primary">
                    
                </Button>
                <DropdownMenu slot="list">
                    <DropdownItem name="resourceSupplier"></DropdownItem>
                    <DropdownItem name="distributor" divided></DropdownItem>
                </DropdownMenu>
            </Dropdown>
        </Row>
        <Row class="margin-top-10">
            <Tabs  v-model="table.query.epType" @on-click="myDoQuery(),initSearchText()">
                <TabPane v-for="(item) in RoleEpType" :label="item.name" :key="item.id" :name="`${item.id}`" >
                    <i-col v-if="item.id == 4">
                        <Row class="margin-top-10">
                            <label class="margin-left-10" style="margin-right:8px;"></label>
                            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px" />
                            <Button class="margin-left-10" type="primary" @click="myDoQuery" icon="md-search"></Button>
                        </Row>
                        <Row class="margin-top-20">
                            <Table :columns="table.col" :data="table.data"></Table>
                            <div style="margin: 10px;overflow: hidden">
                                <div style="float:right;">
                                    <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                                </div>
                            </div>
                        </Row>
                    </i-col>
                    <i-col v-if="item.id == 5">
                        <Row class="margin-top-10">
                            <label class="margin-left-10" style="margin-right:8px;"></label>
                            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px" />
                            <Button class="margin-left-10" type="primary" @click="myDoQuery" icon="md-search"></Button>
                        </Row>
                        <Row class="margin-top-20">
                            <Table :columns="table.col" :data="table.data"></Table>
                            <div style="margin: 10px;overflow: hidden">
                                <div style="float:right;">
                                    <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                                </div>
                            </div>
                        </Row>
                    </i-col>
                    <i-col v-if="item.id == 6">
                        <Row class="margin-top-10">
                            <label class="margin-left-10" style="margin-right:8px;"></label>
                            <Input v-model="table.mQuery.name" placeholder="" clearable style="width: 200px" />
                            <Button class="margin-left-10" type="primary" @click="myDoQuery" icon="md-search"></Button>
                        </Row>
                        <Row class="margin-top-20">
                            <Table :columns="table.col2" :data="table.data"></Table>
                            <div style="margin: 10px;overflow: hidden">
                                <div style="float:right;">
                                    <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                                </div>
                            </div>
                        </Row>
                    </i-col>
                </TabPane>
            </Tabs>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <div style="height: 400px">
                <vue-scroll ref="vScroll">
                    <Form ref="form" :model="vo" :label-width="80" :rules="validate">
                        <Form-item label="" prop="code" v-show="!isUpdate" :error="codeError">
                            <SearchSelect ref="codeSelect" v-model="vo.code" :on-search="searchEpByCode" @blur="searchEpByCode" select-width="auto" @change="changeEp"/>
                        </Form-item>
                        <Form-item label="Logo" prop="logoPic">
                            <OssUpload :files="logoPic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="name">
                            <Input v-model.trim="vo.name" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="legalPerson">
                            <Input v-model.trim="vo.legalPerson" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="licensePic">
                            <OssUpload :files="licensePic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="licensePeriod">
                            <Input v-model.trim="vo.licensePeriod" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="registerCapital">
                            <Input v-model.trim="vo.registerCapital" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="registerDate">
                            <DatePicker v-model="registerDate" type="date" transfer placeholder="" style="width: 150px" :disabled="hasOld"></DatePicker>
                        </Form-item>
                        <Form-item label="" prop="businessScope">
                            <Input v-model.trim="vo.businessScope" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="address">
                            <al-cascader ref="address" v-model="address" :level="2" :disabled="hasOld"/>
                            <Input v-model="vo.streetAddress" placeholder="" :disabled="hasOld"/>
                        </Form-item>
                        <Form-item label="" prop="officeAddress">
                            <al-cascader ref="officeAddress" v-model="officeAddress"  :level="2" :disabled="hasOld"/>
                            <Input v-model="vo.officeStreetAddress" placeholder="" :disabled="hasOld"/>
                        </Form-item>
                        <FormItem label="" prop="status" v-show="isUpdate">
                            <Select transfer v-model="vo.status">
                                <Option v-for="item in EpStatus" :value="item.id" :key="item.id">{{ item.name }}</Option>
                            </Select>
                        </FormItem>
                        <template v-if="!isUpdate">
                            <Divider></Divider>
                            <Form-item label="" prop="user.username">
                                <Input v-model.trim="vo.user.username" :disabled="hasOld"/>
                            </Form-item>
                            <Form-item label="" prop="user.nickname">
                                <Input v-model.trim="vo.user.nickname" :disabled="hasOld"/>
                            </Form-item>
                            <FormItem label="" prop="roles">
                                <Select transfer v-model="vo.roles" filterable multiple :disabled="!this.vo.epType">
                                    <Option v-for="item in roles" :value="item.id" :key="item.id">{{ item.name }}</Option>
                                </Select>
                            </FormItem>
                        </template>
                        <template v-if="!isUpdate">
                            <Divider></Divider>
                            <Form-item label="">
                                <CheckboxGroup v-model="bendType" prop="bankTypeChoice">
                                    <Checkbox label="card" ></Checkbox>
                                    <Checkbox label="ali" ></Checkbox>
                                    <Checkbox label="wechat" ></Checkbox>
                                </CheckboxGroup>
                            </Form-item>
                            <Row class="margin-top-10" v-if="bendType.findIndex(y => Object.is('card', y)) > -1">
                                <Form-item label="" prop="banking.realName">
                                    <Input v-model.trim="vo.banking.realName" :disabled="hasOld"/>
                                </Form-item>
                                <Form-item label="" prop="banking.cardNumber">
                                    <Input v-model.trim="vo.banking.cardNumber" :disabled="hasOld"/>
                                </Form-item>
                                <FormItem label="" prop="banking.bankType">
                                    <Input v-model.trim="vo.banking.bankType" :disabled="hasOld"/>
                                </FormItem>
                            </Row>
                            <Row class="margin-top-10" v-if="bendType.findIndex(y => Object.is('ali', y)) > -1">
                                <Form-item label="" prop="banking.alipayCode">
                                    <Input v-model.trim="vo.banking.alipayCode" :disabled="hasOld"/>
                                </Form-item>
                            </Row>
                            <Row class="margin-top-10" v-if="bendType.findIndex(y => Object.is('wechat', y)) > -1">
                                <Form-item label="" prop="banking.wechatCode">
                                    <Input v-model.trim="vo.banking.wechatCode" :disabled="hasOld"/>
                                </Form-item>
                            </Row>
                        </template>
                    </Form>
                </vue-scroll>
            </div>
        </Modal>
        <Modal v-model="detailModel" :mask-closable="false" :title="showModelTitle" :loading="loadingBtn" :footer-hide="detalFooterHide" @on-cancel="detailCancel">
            <div style="height: 400px">
                <vue-scroll ref="detailVScroll">

                    <template>
                        <div>
                            <Divider></Divider>
                            <CellGroup >
                                <Cell title="" :extra="vo.code"/>
                                <Cell  class="picCell">
                                    <span>logo</span>
                                    <OssUpload v-if="logoPic.length!==0" :files="logoPic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="false"/>
                                </Cell>
                                <Cell title="" :extra="vo.name"/>
                                <Cell title="" :extra="vo.legalPerson"/>
                                <Cell class="picCell">
                                    <span></span>
                                    <OssUpload v-if="licensePic.length!==0" :files="licensePic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="false"/>
                                </Cell>
                                <Cell title="" :extra="vo.licensePeriod"/>
                                <Cell title="" :extra="vo.registerCapital"/>
                                <Cell title="" :extra="vo.registerDate"/>
                                <Cell title="" :extra="vo.businessScope"/>
                                <Cell title="" :extra="addressStr"/>
                                <Cell title="" :extra="officeAddressStr"/>
                                <Cell title="" :extra="statusStr"/>

                            </CellGroup>
                        </div>
                        <div>
                            <Divider></Divider>
                            <CellGroup>
                                <Cell title="" :extra="vo.user.username"/>
                                <Cell title="" :extra="vo.user.nickname"/>
                                <Cell title="" :extra="vo.roleEntity.name"/>
                            </CellGroup>
                        </div>
                        <div>
                            <Divider></Divider>
                            <CellGroup>
                                <Cell title="" :extra="vo.banking.realName"/>
                                <Cell title="" :extra="vo.banking.cardNumber"/>
                                <Cell title="" :extra="vo.banking.bankType"/>
                                <Cell title="" :extra="vo.banking.alipayCode"/>
                                <Cell title="" :extra="vo.banking.wechatCode"/>
                            </CellGroup>
                        </div>
                    </template>

                </vue-scroll>
            </div>
            <div slot="footer" >
            </div>
        </Modal>
    </div>
</template>
<style>
    .picCell .ivu-cell-title{
        width:456px;
        height:127px;
    }
    .picCell .ivu-cell-link .ivu-cell-item .ivu-cell-main .ivu-cell-title>div{
        float: right;
    }
    .picCell .ivu-cell-link .ivu-cell-item .ivu-cell-main .ivu-cell-title>span{
        height: 30%;
        overflow: auto;
        margin: auto;
        position: absolute;
        top: 0; left: 16px; bottom: 0; right: 0;
    }
</style>
<script>
import dayjs from 'dayjs';
import SearchSelect from '@/components/search-select';
import OssUpload from '@/components/upload';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { RoleEpType, EpStatus } from '@/libs/dic';

export default {
    name: 'alluser',
    components: { SearchSelect, OssUpload },
    mixins: [ TableColRender, TableDataView, ModelView ],
    data () {
        return {
            statusStr: null,
            detalFooterHide: true,
            detailAddress: [],
            detailOfficeAddress: [],
            detailModel: false,
            showModelTitle: '',
            EpStatus,
            RoleEpType,
            button1: RoleEpType[0].id,
            button1Value: RoleEpType[0].id,
            bendType: ['card'],
            addUrl: '/api/nmedia/role/create',
            updateUrl: '/api/nmedia/role/update',
            platformTypes: [],
            roles: [],
            address: [],
            addressStr: null,
            officeAddressStr: null,
            officeAddress: [],
            registerDate: null,
            licensePic: [],
            logoPic: [],
            oldEp: null,
            table: {
                col: [{
                    title: '',
                    key: 'id'
                }, {
                    title: '',
                    key: 'name'
                }, {
                    title: '',
                    key: 'adminPhone'
                }, {
                    title: '',
                    key: 'code'
                }, {
                    title: '',
                    key: 'type',
                    render: (h, params) => {
                        const rowData = params.row;
                        const showStr = rowData.epTypes === '4' ? '' : rowData.epTypes === '5' ? '' : rowData.epTypes === '0' ? '' : '';
                        return h('span', showStr);
                    }
                }, {
                    title: '',
                    key: 'addressCode',
                    render: (h, params) => {
                        const rowData = params.row;
                        const showStr = (rowData.address === undefined ? '' : rowData.address) + (rowData.streetAddress === undefined ? '' : rowData.streetAddress);
                        return h('span', showStr);
                    }
                }, {
                    title: '',
                    key: 'status',
                    render: (h, params) => {
                        const rowData = params.row;
                        let showStr = '';
                        for (const i in EpStatus) {
                            if (EpStatus[i].id === rowData.status) {
                                showStr = EpStatus[i].name;
                            }
                        }
                        return h('span', showStr);
                    }
                }, {
                    title: '',
                    key: 'registerDate',
                    render: this.renderDate
                }, {
                    title: '',
                    key: 'action',
                    width: 400,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [
                        {
                            type: 'switch',
                            click: () => this.status(params.row),
                            bool: params.row.status === 1,
                            config: { loading: this.loadingBtn,
                                style: { marginRight: '5px' }
                            }
                        },
                        {
                            config: {
                                loading: this.loadingBtn,
                                style: { marginRight: '5px' }
                            },
                            click: () => this.showDetail(params.row),
                            text: ''
                        },
                        {
                            type: 'resetPassword',
                            click: () => this.resetEpAdminPassword(params.row),
                            epName: params.row.name,
                            config: { loading: this.loadingBtn,
                                style: { marginRight: '5px' }
                            },
                            text: ''
                        }, {
                            config: { loading: this.loadingBtn,
                                style: { marginRight: '5px' }
                            },
                            click: () => this.update(params.row),
                            text: ''
                        }
                    ])
                }],
                col2: [{
                    title: '',
                    key: 'id'
                }, {
                    title: '',
                    key: 'nickname'
                }, {
                    title: '',
                    key: 'phone'
                }, {
                    title: 'open id',
                    key: 'adminPhone'
                }, {
                    title: '',
                    key: 'gender',
                    render: (h, params) => {
                        const rowData = params.row;
                        return h('span', rowData.gender === 1 ? '' : rowData.gender ? '' : null);
                    }
                }, {
                    title: '',
                    key: 'idCard'
                }, {
                    title: 'QQ',
                    key: 'qq'
                }, {
                    title: '',
                    key: 'email'
                }, {
                    title: '',
                    key: 'address',
                    render: (h, params) => {
                        const rowData = params.row;
                        const showStr = (rowData.address === undefined ? '' : rowData.address) + (rowData.addressDetail === undefined ? '' : rowData.addressDetail);
                        return h('span', showStr);
                    }
                }
                // , {
                //     title: '',
                //     key: 'action',
                //     width: 400,
                //     align: 'center',
                //     render: (h, params) => this.renderActions(h, params, [
                //         {
                //             onfig: {
                //                 loading: this.loadingBtn,
                //                 style: { marginRight: '5px' }
                //             },
                //             click: () => this.$router.push({
                //                 name: 'UserInfo',
                //                 query: { id: params.row.id, name: params.row.name }
                //             }),
                //             text: ''
                //         }
                //     ])
                // }
                ],
                url: '/api/nmedia/role/list',
                memberUrl: '/api/member/list',
                query: {
                    name: null,
                    epType: `${RoleEpType[0].id}`,
                    isAudit: false
                },
                mQuery: {
                    name: ''
                }
            },
            validate: {
                code: [{ required: true, trigger: 'blur', message: '' }],
                name: [{ required: true, trigger: 'blur', message: '' }],
                'user.username': [{ required: true, trigger: 'blur', message: '' }],
                'user.password': [{ required: true, trigger: 'blur', message: '' }],
                roles: [{ required: true, type: 'array', trigger: 'change', message: '' }],
                'banking.realName': [{ required: true, trigger: 'blur', message: '' }],
                'banking.cardNumber': [{ required: true, trigger: 'blur', message: '' }],
                'banking.bankType': [{ required: true, trigger: 'blur', message: '' }],
                'banking.alipayCode': [{ required: true, trigger: 'blur', message: '' }],
                'banking.wechatCode': [{ required: true, trigger: 'blur', message: '' }],
                bankTypeChoice: [{ required: true, trigger: 'change', message: '' }]
            },
            vo: {
                code: null,
                name: null,
                legalPerson: null,
                licensePeriod: null,
                registerCapital: null,
                registerDate: null,
                businessScope: null,
                addressCode: null,
                address: null,
                streetAddress: null,
                officeAddressCode: null,
                officeAddress: null,
                officeStreetAddress: null,
                licensePic: null,
                logoPic: null,
                status: 1,
                user: {
                    username: null,
                    phone: null,
                    nickname: null
                },
                banking: {
                    realName: null,
                    cardNumber: null,
                    bankType: null,
                    alipayCode: null,
                    wechatCode: null
                },
                roles: [],
                epType: null,
                roleEntity: {
                    name: null
                }
            }
        };
    },
    computed: {
        hasOld () {
            return !!this.oldEp;
        },
        codeError () {
            return this.oldEp?.has ? '' : null;
        },
        isRoot () {
            return this.$store.state.user.info?.platform?.type === 0;
        },
        userPlatform () {
            return this.$store.state.user.info?.platform?.id;
        },
        generateAddOrUpdate () {
            const last = this.address[this.address.length - 1] || {};
            const office = this.officeAddress[this.officeAddress.length - 1] || {};
            return { ...this.vo,
                addressCode: last.code,
                address: last.name,
                officeAddressCode: office.code,
                officeAddress: office.name,
                registerDate: this.registerDate && dayjs(this.registerDate).format('YYYY-MM-DD'),
                licensePic: this.licensePic?.length ? this.licensePic[0].name : null,
                logoPic: this.logoPic?.length ? this.logoPic[0].name : null
            };
        },
        transformAddrToStr () {
            const last = this.address[this.address.length - 1] || {};
            const office = this.officeAddress[this.officeAddress.length - 1] || {};
            return { ...this, addressStr: last.name, office: office.name };
        }
    },
    watch: {
        'vo.user.username' (val) {
            this.vo.user.phone = val;
        }
    },
    methods: {
        create (name) {
            this.vo.epType = name === 'resourceSupplier' ? 4 : 5;
            this.add();
        },
        async add () {
            await this.loadAllRoles();
            this.$super(ModelView).add();
            this.oldEp = null;
            this.registerDate = null;
            this.licensePic = [];
            this.logoPic = [];
            this.$refs.codeSelect.clear();
            this.vo.streetAddress = null;
            this.vo.addressCode = null;
            this.vo.officeStreetAddress = null;
            this.vo.officeAddressCode = null;
            this.$nextTick(() => {
                this.$refs.address.$children[0].clearSelect();
                this.$refs.officeAddress.$children[0].clearSelect();
            });
        },
        async loadAllRoles () {
            const result = await this.get('/api/permission/role/list', { epType: this.vo.epType, type: 1 });
            if (result.success) {
                this.roles = result.value;
            }
        },
        changeEp (item) {
            this.oldEp = item;
            ['code', 'name', 'legalPerson', 'licensePeriod', 'registerCapital', 'registerDate', 'businessScope']
                .filter(k => Reflect.has(item, k)).forEach(k => this.vo[k] = item[k]);
            this.setByItem(item);
            this.vo.user.username = item.admin.username;
            this.vo.user.nickname = item.admin.nickname;
        },
        async searchEpByCode () {
            if (!this.vo.code) {
                this.oldEp = null;
                return [];
            }
            const result = await this.get(`/api/ep/code/${this.vo.code}`, { admin: true, platform: true });
            if (result.success && result.value) {
                const has = !!result.value.epPlatform.some(p => p.platform === this.userPlatform);
                this.changeEp({ ...result.value, has });
            } else {
                this.oldEp = null;
            }
            return [];
        },
        async status (item) {
            const result = await this.fetch(`/api/nmedia/role/status/${item.id}`, { method: 'post', params: { status: (item.status === 1 ? '9' : item.status === 9 ? '1' : null) } });
            if (result.success) {
                this.myDoQuery();
            }
        },
        async myDoQuery () {
            const before = await this.beforeQuery();
            if (before !== false) {
                let res = null;
                if (this.table.query.epType === '6') {
                    res = await this.get(this.table.memberUrl, { ...this.table.mQuery });
                } else {
                    res = await this.get(this.table.url, { ...this.table.query });
                }
                if (res.success && res.value) {
                    const data = await this.processQueryData(
                        res.value.list ? res.value.list : res.value || [], res
                    );
                    this.table.data = data;
                    this.table.total = Number(res.value.total) || this.table.data.lenght;
                } else {
                    this.table.data = [];
                    this.table.total = 0;
                }
            }
            this.loadingBtn = false;
            await this.afterQuery();
        },
        async initSearchText () {
            this.table.query.name = null;
        },
        async update (item) {
            this.oldEp = null;
            this.$super(ModelView).update(item);
            this.setByItem(item);
        },
        setByItem (item) {
            if (item.addressCode) {
                this.setAddress('address', item.addressCode);
            }
            if (item.officeAddressCode) {
                this.setAddress('officeAddress', item.officeAddressCode);
            }
            if (item.registerDate) {
                this.registerDate = dayjs(item.registerDate).toDate();
            }
            if (item.licensePic) {
                this.licensePic = [{ name: item.licensePic, status: true }];
            }
            if (item.logoPic) {
                this.logoPic = [{ name: item.logoPic, status: true }];
            }
        },
        setAddress (name, val) {
            this[name] = [
                val.substring(0, 2).padEnd(6, '0'),
                val.substring(0, 4).padEnd(6, '0'),
                val
            ];
        },
        async cancel () {
            this.$super(ModelView).cancel();
            this.$nextTick(() => {
                this.$refs.address.$children[0].clearSelect();
                this.$refs.officeAddress.$children[0].clearSelect();
            });
            this.vo.addressCode = null;
            this.vo.address = null;
            this.vo.streetAddress = null;
            this.vo.officeAddressCode = null;
            this.vo.officeAddress = null;
            this.vo.officeStreetAddress = null;
            this.$refs.form.resetFields();
        },
        async resetEpAdminPassword (item) {
            const result = await this.fetch(`/api/ep/reset-password/${item.id}`, { method: 'post' });
            if (result.success) {
                this.$Message.info('');
            }
        },
        showDetail (item) {
            this.cancel();
            this.detailModel = true;
            this.detailVScrollToTop();
            // this.setByItem(item);
            Object.keys(this.vo).filter(key => Reflect.has(item, key)).forEach(key => {
                if (key === 'status') {
                    this.statusStr = item[key] === 1 ? '' : '';
                } else if (key === 'address') {
                    this.addressStr = item[key];
                } else if (key === 'officeAddress') {
                    this.officeAddressStr = item[key];
                } else if (key === 'registerDate') {
                    this.vo[key] = dayjs(item[key]).toDate().toString();
                } else if (key === 'licensePic') {
                    this.licensePic = [{ name: item[key], status: true }];
                } else if (key === 'logoPic') {
                    this.logoPic = [{ name: item[key], status: true }];
                } else {
                    this.vo[key] = item[key];
                }
            });
            if (item.admin) {
                Object.keys(this.vo.user).filter(key => Reflect.has(item.admin, key)).forEach(key => this.vo.user[key] = item.admin[key]);
            }
            if (item.banking) {
                Object.keys(this.vo.banking).filter(key => Reflect.has(item.banking, key)).forEach(key => this.vo.banking[key] = item.banking[key]);
            }
            if (item.roleEntity) {
                Object.keys(this.vo.roleEntity).filter(key => Reflect.has(item.roleEntity, key)).forEach(key => this.vo.roleEntity[key] = item.roleEntity[key]);
            }
        },
        detailVScrollToTop (ref = 'detailVScroll') {
            if (this.$refs[ref]) {
                this.$refs[ref].scrollTo({ x: 0, y: 0 });
            }
        },
        detailCancel () {
            this.logoPic = [];
            this.licensePic = [];
            Object.keys(this.vo).forEach(key => {
                if (key === 'status') {
                    this.vo[key] = 1;
                } else if (key === 'roles') {
                    this.vo[key] = [];
                } else if (key === 'user' || key === 'banking' || key === 'roleEntity') {
                    Object.keys(this.vo[key]).forEach(key1 => this.vo[key][key1] = null);
                } else {
                    this.vo[key] = null;
                }
            });
        }
    }
};
</script>

